import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, LotacaoTributaria } from '../../../../models/dtos/mfp/lotacaoTributaria';
import { TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoLotacaoTributariaEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'tipoLotacaoTributaria',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const LotacaoTributariaSearchView: FC<SearchViewProps<LotacaoTributaria>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const columns: ColumnsProps[] = [
    enumColumn(
      {
        dataField: 'tipoLotacaoTributaria',
        editable: false,
        sort: true,
        text: 'Tipo de Lotação',
      },
      TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES
    ),
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção de Lotação Tributária"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={527}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'LotacaoTributariaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default LotacaoTributariaSearchView;
